@font-face {
  font-family: "Oswald";
  src: url("../font/Oswald-Light.woff2") format("woff2"), url("../font/Oswald-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Oswald";
  src: url("../font/Oswald-Regular.woff2") format("woff2"), url("../font/Oswald-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote {
  margin: 0;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

textarea {
  resize: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

html,
body {
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a,
input,
textarea,
button {
  outline: 0;
  border: 0;
}

button {
  background-color: transparent;
  cursor: pointer;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  display: inline-block;
}

/* change colours to suit your needs */

ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */

mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
}

.container {
  max-width: 1230px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

body {
  font-family: "Oswald", "Helvetica", "Arial", sans-serif;
  font-weight: 300;
  font-size: 18px;
  line-height: 1.5;
  color: #2C2B2B;
  background-color: #fff;
  line-height: 1.5;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title {
  font-weight: 400;
  font-size: 72px;
  color: #fff;
  line-height: 1.4861111111;
  text-transform: uppercase;
}

.subtitle {
  font-weight: 400;
  font-size: 36px;
  color: #21062A;
  line-height: 0.7361111111;
  text-transform: uppercase;
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #fff;
  background-color: #5A0D67;
  transition: color 0.3s, background-color 0.3s ease-in-out;
}

.btn-bg:hover {
  color: #5A0D67;
  background-color: #fff;
}

.logo {
  width: 197px;
  height: 53px;
  fill: #fff;
  stroke: #fff;
  display: inline-block;
  transition: fill 0.3s, stroke 0.3s ease-in-out;
}

.logo:hover {
  fill: #5A0D67;
  stroke: #5A0D67;
}

.header {
  background-image: url(../img/decore/header-bg.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  min-height: 700px;
  color: #fff;
}

.header__top {
  padding-top: 47px;
  display: flex;
  justify-content: space-between;
}

.header__phone {
  font-size: 36px;
  font-weight: 400;
}

.header__burger {
  display: none;
}

.header__content {
  padding-top: 104px;
  position: relative;
}

.header__title {
  line-height: 1.5;
}

.header__subtitle {
  color: #fff;
  margin-bottom: 40px;
}

.header__text {
  margin-bottom: 30px;
  max-width: 800px;
}

.header__btn {
  font-size: 14px;
  text-transform: uppercase;
  padding: 17px 54px;
}

.header__social {
  padding: 70px 0 20px 65px;
}

.header__img {
  position: absolute;
  right: 0;
  bottom: -294px;
}

.social {
  display: flex;
  align-items: center;
}

.social__item:not(:last-child) {
  margin-right: 10px;
}

.social__link {
  fill: #fff;
}

@media (max-width: 1130) {
  body {
    font-size: calc(16px + 3.4px * ((100vw - 320px) / 1230));
  }

  .title {
    font-size: calc(36px + 61.2px * ((100vw - 320px) / 1230));
  }

  .subtitle {
    font-size: calc(23px + 83.3px * ((100vw - 320px) / 1230));
  }
}